#diamond-shape {
    width: 0;
    height: 0;
    border: 50px solid transparent;
    border-bottom-color: red;
    position: relative;
    top: -50px;
}

#diamond-shape:after {
    content: '';
    position: absolute;
    left: -50px;
    top: 50px;
    width: 0;
    height: 0;
    border: 50px solid transparent;
    border-top-color: red;
}
/**************************************MAIN SUB TOP**********************************************/

/*code for the text content block*/

/*This code uses flex to control the inline functionality*/
.subpage-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    width: 100%;
    height: auto;
    background-color: $color-clear;
}

.diamonds-small > .col-md-12 {
    padding-right: 0 !important;
    padding-left: 0 !important;
}

.pattern-sub > .col-md-12 {
    padding-right: 0 !important;
    padding-left: 0 !important;
}

/*This code controls the content block for the subpages START */
#main-container-flex {
    width: 100%;
    height: auto;
    margin: 0 auto;
    background-color: $color-clear;
    padding: 10px 10px;
}

.main-container-wrapper {
    position: relative;
    top: 0px;
    left: 0px;
    display: block;
    width: 100%;
    max-width: 1024px;
    height: auto;
    margin: 0 auto;
    padding: 10px 10px;
    background-color: $color-clear;
}

.main-container-diamonds-md {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background: url("/Static/img/diamonds-md.png") repeat-y left top;
    background-color: $color-solid-bg;
}

.main-container-wrapper p {
    margin: 14px auto;
    max-width: 1000px;
    text-align: left;
    color: #FFF;
    text-indent: each-line;
    font-family: Arial, sans-serif;
}

.main-container-wrapper hgroup {
    background-color: #d6cfc7;
    color: #252525;
    width: 100%;
    padding: 0 8px;
    margin-bottom: 10px;
    line-height: 43px !important;
    font-family: "bebas-neue";
    font-size: 25px;
    font-weight: normal;
}

.main-container-left {
    position: relative;
    top: 0px;
    left: 0px;
    width: 100%;
    height: auto;
    display: block;
    padding: 0;
    background-color: $color-clear;
}

.main-container-left img {
    position: relative;
    top: 0px;
    left: 5%;
}

.main-container-right {
    position: relative;
    top: 0px;
    left: 0px;
    width: 100%;
    height: auto;
    display: block;
    padding: 0;
    background-color: $color-clear;
}

.block-p {
    position: relative;
    top: 0px;
    left: 5%;
    display: block;
    margin: 15px auto;
    width: 100%;
    text-align: center;
    color: #FFF;
    font-size: 18px;
    font-weight: 200;
    font-family: Arial, sans-serif;
    text-align: left;
    line-height: 35px;
    max-width: none;
    background-color: $color-clear;
}

.textwrap-right {
    clear: left;
    float: right;
    margin: 10px;
}

.textwrap-left {
    clear: right;
    float: left;
    margin: 10px;
}

.main-container-button {
    position: relative;
    top: 0;
    left: 0px;
    float: right;
    display: block;
    width: 100%;
    height: 50px;
    margin: 0 0 0 0;
    background-color: $color-clear;
}

.main-container-button .bttn {
    position: relative;
    top: 0px;
    left: 35%;
    margin: 8px;
    border: 0px;
    border-radius: initial;
    padding: 14px 15px;
    text-transform: uppercase;
    color: #FFF;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42857143;
    white-space: nowrap;
    vertical-align: middle;
}

.bttn-2, .bttn {
    position: relative;
    top: 0px;
    left: 0px;
    margin: 8px;
    border: 0px;
    border-radius: initial;
    padding: 14px 15px;
    text-transform: uppercase;
    text-decoration: none;
    color: #FFF;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42857143;
    white-space: nowrap;
    vertical-align: middle;
}

.bttn-2:hover {
    color: #857a7a;
}

.bttn:hover {
    color: #857a7a;
}

.main-container-button a:hover {
    color: #857a7a;
}

.main-container-left-L {
    position: relative;
    top: 0px;
    left: 0px;
    width: 100%;
    height: auto;
    display: block;
    padding: 0;
    background-color: $color-clear;
}

.main-container-right-R {
    position: relative;
    top: 0px;
    left: -26px;
    width: 100%;
    height: auto;
    display: block;
    padding: 0;
    background-color: $color-clear;
}

/*This code controls the content block for the subpages END */


/**************************************MAIN SUB MID**********************************************/
/*SubPage - Main Code Start ONE*/

.solid-background {
    background-color: #efece6;
}

.exp-wrapper {
    position: relative;
    top: 0px;
    left: 0px;
    float: left;
    width: 100%;
    padding: 3px;
    margin: 25px;
}

.content-border1 {
    width: 100%;
    height: 100%;
    padding: 20px;
}

.content-border2 {
    width: 100%;
    height: 100%;
    border: #d5d0c6 solid 1px;
    padding: 20px;
}

.content-border2:after {
    content: ".";
    visibility: hidden;
    display: block;
    height: 0;
    clear: both;
}

article {
    position: relative;
    top: 0px;
    left: 0px;
    float: left;
    width: 100%;
    border: #d5d0c6 solid 4px;
    padding: 3px;
    margin: 25px;
}

article .bttn {
    position: relative;
    top: 0px;
    left: 0px;
    margin: 10px 0 0 40%;
}


article .hr-diamonds {
    position: relative;
    top: 0px;
    left: 13px;
    width: auto;
    max-width: 330px;
    height: 16px;
    background-size: 330px 16px;
    background: url("/Static/img/hr-diamond.png") center center no-repeat;
}

.diamonds-small {
    width: 100%;
    background: url('/Static/img/diamonds-sm.png') center center no-repeat;
    height: 100%;
    max-height: none;
    background-size: 100% auto;
    padding: 0px;
    overflow: hidden;
}

.diamond-bar {
    background: url('/Static/img/diamonds-sm.png') repeat-x;
    margin: 0px;
    width: 100%;
    border: 0;
    height: 20px;
}

.diamond-bar-pattern {
    background: url('/Static/img/diamond-bar.png') repeat-x;
    margin: 0px;
    width: 100%;
    border: 0;
    height: 20px;
}

/*SubPage - Main Code Start TWO*/
.pattern-sub {
    width: 100%;
    background: url('/Static/img/patterned-bg.png') center center no-repeat;
    height: auto;
    max-height: 100%;
    padding: 0px 0px;
    overflow: hidden;
}

/*Code for Image Block Section Area Three*/

.diamonds-middle {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background: url('/Static/img/diamonds-md.png') repeat-y left top;
}


/*SubPage - Main Code Start FOUR*/

#epi-scaler_sub {
    min-width: 1424px;
    height: auto;
    background-size: 100% auto;
    padding: 0px 0px;
    overflow: hidden;
}

.pattern-sub-02 {
    width: 100%;
    margin: 0px;
    padding: 50px 0px 70px;
    background: url("/Static/img/patterned-bg-2.jpg") left top no-repeat;
    background-size: 100% 100%;
    overflow: hidden;
}

#subpage-holder {
    position: relative;
    top: 0px;
    left: -30px;
    display: -webkit-row;
    display: flex;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    margin: 4px;
    padding: 5px;
    -webkit-flex: row;
    flex: row;
    order: 3;
    width: 100%;
    height: auto;
    margin: 0 auto;
    max-width: 1200px;
    margin: 0 auto;
    list-style-type: none;
    padding: 0;
    background-color: transparent;
}

.subpage-images-01 {
    display: -webkit-row;
    display: flex;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    margin: 4px;
    padding: 5px;
    -webkit-flex: row;
    flex: row;
    order: 3;
}

.subpage-images-flex {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: auto;
    margin-left: 5px;
    z-index: 3;
    background-color: transparent;
}
/*image*/
.su-image-01 {
    position: relative;
    top: 0px;
    left: 0px;
    display: block;
    background-color: transparent;
}

.su-image-01 img {
    max-width: 180px;
    border: #c7c7c7 5px solid;
    margin: auto;
}

/*button*/
.su-text-01 {
    position: relative;
    top: -63px;
    left: 29%;
    display: block;
    width: 112px;
    height: 35px;
    margin: 80px 0 0 0;
    padding: 10px;
    color: white;
    text-align: center;
    background-color: $color-black;
}

.su-text-02 {
    position: relative;
    top: 0px;
    left: 20px;
    display: block;
    width: 53%;
    min-width: 250px;
    background-color: transparent;
}

.su-text-02 p {
    font-family: BellMt;
    font-style: italic;
    padding: 10px 0 25px;
}


/* Code for the subpage section END */

