/*Code for One Of Development Block Section Area Six*/

#footer-container p {
	font-size: 13px;
	text-align:left;
	color:#acacac;
}
#footer-container ul {
    list-style-type:none;
    margin:0;
	padding:0;
}
#footer-container ul li {
	padding:2.5px 0;
    font-size: 14px;
    font-weight: 700;
}
#footer-container h3 {
	color:#acacac;
	text-align:left;
}
#footer-container a {
	color:#acacac;
	text-decoration:none;
}
.foot-col-3:after {
   content: "."; 
   visibility: hidden; 
   display: block; 
   height: 0; 
   clear: both;
}
#copyright {
    display: block;
    position: relative;
    top: -25px;
    left: 0px;
    width: 100%;
    height: 25px;
    text-align: -webkit-center;
    font-size: 10px;
    font-weight: 200;
    padding: 20px 0;
    background-color: transparent;
}

/*-----------------------------------End Footer-------------------------*/